<template>
  <v-card :max-width="mobile ? '95%' : '80%'">
    <v-card-title class="secondary pa-0">
      <v-img
        :max-height="mobile ? '20vh' : '125px'"
        position="center right"
        contain
        :src=picture
      />
    </v-card-title>
    <v-card-text class=pa-4>
      <v-row v-if="!mobile">
        <v-col
          cols="12"
          class="pa-0 pa-sm-4 font-weight-light font-italic text-sm-body-1 text-justify"
        >
          {{ t('cover') }}
          <v-divider class="my-2" />
        </v-col>
      </v-row>
      <v-row>
        <v-col
          v-for="item in contactCard"
          :key=item.label
          cols=12
          md=6
          class="py-0"
        >
          <v-textarea
            auto-grow
            :dense=mobile
            :label="t(item.label)"
            no-resize
            outlined
            :prepend-inner-icon="item.icon || undefined"
            readonly
            rows=1
            :value="item.value"
            class="text-capitalize"
          />
        </v-col>
      </v-row>
      <v-row>
        <v-col
          cols="12"
          class="font-weight-light text-justify"
        >
          <span class="primary--text text-capitalize subtitle-1">
            <v-icon color="primary" v-text="'$misc'" />
            {{ t('misc') }}
          </span>
          <v-divider class="my-2" />
          <v-chip
            v-for="interest in tr('interests')"
            :key=interest.value
            class="mx-1"
            label
            color="primary"
            outlined
          >
            <v-avatar left>
              <v-icon v-text="`$${interest.icon}`" />
            </v-avatar>
            {{ t(interest.value) }}
          </v-chip>
          <p class="mt-2 primary--text">
            {{ t('Proud father of') }} {{ tr('cv.misc.fatherOf') }}
            <v-icon color="primary" v-text="'$child'" />
            <v-icon color="primary" v-text="'$boy'" />
          </p>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  name: 'cvContact',
  props: {
    contact: {
      type: Object,
      default: () => {},
    },
    picture: {
      type: String,
      default: '//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png',
    },
  },
  computed: {
    address() {
      const {
        city,
        country,
        province,
        postalCode,
      } = this.contact.address;
      const loc = `${city} - ${postalCode} (${province})`;
      return this.countryCode
        ? `${loc}, ${country.name}`
        : loc;
    },
    age() {
      return Math.floor(
        (new Date() - new Date(this.contact.dateOfBirth).getTime())
        / 3.15576e+10,
      );
    },
    contactCard() {
      return [
        {
          label: 'name',
          value: this.contact.name,
        },
        {
          label: 'surnames',
          value: this.contact.surnames,
        },
        {
          label: 'email',
          value: this.contact.email,
          icon: '$email',
        },
        {
          label: 'address',
          value: this.address,
          icon: '$address',
        },
        {
          label: 'age',
          value: this.age,
          icon: '$birthday',
        },
      ];
    },
    countryCode() {
      return this.$vuetify.lang.current !== 'es';
    },
    phone() {
      const prefix = this.countryCode
        ? `(+${this.contact.phone.prefix}) `
        : '';
      return `${prefix}${this.contact.phone.number}`;
    },
  },
};
</script>

<style />
